.cabinet-main {
  padding-bottom: 40px;
  .container {
    max-width: 1640px;
    padding: 0 10px;
  }
  &__inner {
    position: relative;
  }
  &__title {
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 32px;
    background: rgba(0, 0, 0, 0.4);
    padding: 20px 100px;
    span {
      position: relative;
      font-size: 28px;
      line-height: 120%;
      font-weight: 300;
      color: rgba(255, 255, 255, 0.8);
      text-transform: uppercase;
      letter-spacing: 1.4px;
      &:not(:last-child) {
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: -18px;
          transform: translate(0, -50%);
          width: 5px;
          height: 5px;
          background: #F07933;
          border-radius: 50%;
        }
      }
    }

  }
  &__img {
    img {
      width: 100%;
    }
  }
}
.cabinet-info {
  position: relative;
  padding: 40px 0;
  &__top {
    max-width: 1200px;
    text-align: center;
    margin: 0 auto 100px;
    .cabinet-info__text {
      margin-top: 30px;
      p {
        font-size: 22px;
        letter-spacing: 1.76px;
      }
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    p {
      font-size: 17px;
      line-height: 120%;
      font-weight: 300;
      color: #696971;
      letter-spacing: 1.02px;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  &__desc {
    width: 33%;
  }
  &__img {
    width: 67%;
    img {
      width: 100%;
    }
  }
  &__subtitle {
    border-bottom: 1px solid #000;
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
}
.cabinet-gallery {
  position: relative;
  padding: 40px 0;
  &__inner {
    max-width: 1023px;
    margin: 0 auto;
  }
  .swiper {
    overflow: unset;
  }
  .swiper-slide {
    img {
      width: 100%;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    transform: translate(0, -50%);
    width: 250px;
    height: 250px;
    background: rgba(0, 0, 0, 0.3);
    border-radius: 50%;
    margin-top: 0;
    &:after {
      display: none;
    }
    svg {
      width: 42%;
      height: auto;
    }
  }
  .swiper-button-prev {
    left: 80px;
  }
  .swiper-button-next {
    right: 80px;
  }
}
.cabinet-services {
  padding: 40px 0;
  &__top {
    margin-bottom: 60px;
  }
  &__title {
    border-bottom: 1px solid #000000;
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
  &__text {
    p {
      font-size: 17px;
      line-height: 120%;
      font-weight: 300;
      color: #696971;
      letter-spacing: 1.02px;
    }
  }
  &__group {
    &:not(:first-child) {
      margin-top: 80px;
    }
  }
  &__subtitle {
    font-size: 19px;
    line-height: 120%;
    font-weight: 500;
    color: #505060;
    letter-spacing: 0.76px;
    margin-bottom: 20px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 120px / 4));
    gap: 40px;
    margin-top: 40px;
  }
  &__item {
    &-img {
      img {
        width: 100%;
      }
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 8px;
      margin-top: 30px;
      li {
        font-size: 17px;
        line-height: 120%;
        font-weight: 300;
        color: #696971;
        letter-spacing: 1.02px;
      }
    }
  }
}
.cabinet-connection {
  padding: 40px 0;
  .container {
    max-width: 1600px;
  }
  &__inner {
    background: rgb(255, 255, 255);
    box-shadow: 0px 4px 14px 0px rgba(0, 0, 0, 0.09);
    padding: 40px;
  }
  &__title {
    text-transform: unset;
  }
  .cabinet-form {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 120px / 4));
    gap: 32px 40px;
    margin-top: 32px;
    &__check {
      grid-column: 1/5;
    }
  }
}
.cabinet-form {
  &__group {
    position: relative;
    label {
      position: absolute;
      z-index: 1;
      top: -5px;
      left: 12px;
      display: inline-flex;
      align-items: center;
      background: #fff;
      padding: 0 2px;
      font-size: 12px;
      line-height: 120%;
      font-weight: 300;
      color: #696971;
      letter-spacing: 0.72px;
      span {
        display: flex;
        align-items: center;
        font-size: 18px;
        color: #F07A34;
      }
    }
    input {
      width: 100%;
      height: 52px;
      background: transparent;
      border: 1px solid #F2F2F7;
      padding: 0 20px;
      font-size: 17px;
      line-height: 120%;
      font-weight: 300;
      letter-spacing: 1.02px;
      &::placeholder {
        color: #B0B0BE;
      }
    }
    textarea {
      width: 100%;
      height: 52px;
      align-content: center;
      background: transparent;
      border: 1px solid #F2F2F7;
      padding: 0 20px;
      font-size: 17px;
      line-height: 120%;
      font-weight: 300;
      letter-spacing: 1.02px;
      resize: none;
      &::placeholder {
        color: #B0B0BE;
      }
    }
  }
  &__btn {
    width: 100%;
    height: 52px;
    span {
      font-size: 17px;
      line-height: 120%;
      font-weight: 300;
      text-transform: uppercase;
      letter-spacing: 1.02px;
      padding: 0 16px;
    }
  }
  &__check {
    font-size: 12px;
    line-height: 120%;
    font-weight: 300;
    color: #696971;
    letter-spacing: 0.72px;
    text-decoration: underline;
    text-underline-offset: 2px;
  }
}
.cabinet-info2 {
  padding: 40px 0;
  &__title {
    border-bottom: 1px solid #000000;
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    p {
      font-size: 17px;
      line-height: 120%;
      font-weight: 300;
      color: #696971;
      letter-spacing: 1.02px;
    }
  }
  &__img {
    position: relative;
    width: 72%;
    margin: 60px auto 0;
    img {
      width: 100%;
    }
  }
  .numbers {
    span {
      position: absolute;
      width: 46px;
      height: 46px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #FFFFFF;
      border-radius: 50%;
      font-size: 21px;
      line-height: 105%;
      letter-spacing: 1.68px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    margin-top: 60px;
  }
  &__item {
    display: flex;
    gap: 20px;
    &-number {
      width: 46px;
      height: 46px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #E8E8EE;
      border-radius: 50%;
      font-size: 21px;
      line-height: 105%;
      letter-spacing: 1.68px;
      margin-top: -12px;
    }
    &-title {
      font-size: 19px;
      line-height: 120%;
      font-weight: 500;
      color: #505060;
      letter-spacing: 0.76px;
    }
    &-text {
      font-size: 17px;
      line-height: 120%;
      font-weight: 300;
      color: #696971;
      letter-spacing: 1.02px;
      margin-top: 8px;
    }
  }
}
.cabinet-product {
  padding: 40px 0 80px;
  &__title {
    border-bottom: 1px solid #000000;
    padding-bottom: 20px;
    margin-bottom: 20px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    p {
      font-size: 17px;
      line-height: 120%;
      font-weight: 300;
      color: #696971;
      letter-spacing: 1.02px;
    }
  }
  &__img {
    margin: 40px 0;
    img {
      width: 100%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 40px / 2));
    gap: 40px;
    margin-top: 40px;
  }
  &__item {
    img {
      width: 100%;
    }
  }
}
.fixed-btns__wrap {
  .fixed-btns {
    position: fixed;
    z-index: 3;
    bottom: 10px;
    right: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
  }
  .fixed-btns2 {
    position: fixed;
    z-index: 3;
    bottom: 10px;
    left: 20px;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 15px;
  }
  .fixed-btn {
    height: 44px;
    padding: 5px 16px;
    span {
      font-size: 15px;
      line-height: 120%;
      color: #000000;
    }
  }
  .btn-prev,
  .btn-next {
    width: 44px;
    height: 44px;
    padding: 0;
    svg {
      max-width: 70%;
      width: auto;
      height: auto;
    }
  }
  .btn-prev {
    justify-content: flex-end;
  }
  .btn-next {
    justify-content: start;
  }
  .btn-primary {
    span {
      color: #fff;
    }
  }
  .btn-white {
    background: rgba(255, 255, 255, 0.8);
    border: 1px solid #444444;
    &:hover {
      background: #e9e9e9;
    }
  }
}


