.btn.disabled,.btn[disabled],fieldset[disabled] .btn {
  cursor: not-allowed;
  pointer-events: none
}
.btn-primary {
  color: #fff;
  background: #F07A34;
}
.btn-primary:focus,
.btn-primary:hover {
  background: #ff5e00;
}
.btn-primary.active,
.btn-primary:active {
  background: #ff831c;
}
.btn-primary.disabled,
.btn-primary[disabled] {
  background: #dbdee2;
}
.breadcrumbs {
  padding: 20px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 22px;
  }
  &__item {
    position: relative;
    font-size: 13px;
    line-height: 120%;
    color: #696971;
    letter-spacing: 0.65px;
    &--active {
      font-weight: 500;
      color: #F07A34;
    }
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -11px;
        width: 2px;
        height: 2px;
        background: #696971;
        border-radius: 50%;
      }
    }
    &:hover {
      color: #F07A34;
    }

  }
}
.text34 {
  font-size: 34px;
  line-height: 120%;
  text-transform: uppercase;
  letter-spacing: 1.7px;
}
.text21 {
  font-size: 21px;
  line-height: 120%;
  text-transform: uppercase;
  letter-spacing: 1.68px;
}
.modal {
  background: rgba(#000, .6);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 400px;
    max-width: 100%;
    position: relative;
    background: #ffffff;
    box-shadow: 0px 4px 14px 0px rgba(0, 0, 0, 0.09);
    margin: 0 auto;
    padding: 40px 30px 30px;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 30px;
    right: 15px;
    background: transparent;
    border: none;
    padding: 0;
    width: 14px;
    cursor: pointer;
    img {
      width: 100%;
    }
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    margin-top: 40px;
  }
}

@media screen and (max-width: 1300px) {
  .text34 {
    font-size: 29px;
    letter-spacing: 1.45px;
  }
  .text21 {
    font-size: 18px;
    letter-spacing: 1.44px;
  }
}

@media screen and (max-width: 429px) {
  .text34 {
    font-size: 23px;
    letter-spacing: 1.15px;
  }
}