.footer {
  background: #F6F6F6;
}
.footer-top {
  padding: 80px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 40px;
  }
  &__items {
    display: flex;
    gap: 100px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
    li a {
      font-size: 15px;
      line-height: 120%;
      text-transform: uppercase;
      letter-spacing: 1.2px;
      &:hover {
        color: #F07A34;
      }
    }
  }
  &__right {
    text-align: right;
  }
  &__group {
    display: flex;
    flex-direction: column;
  }
  &__phone {
    font-size: 35px;
    line-height: 120%;
    font-weight: 300;
    letter-spacing: 2.1px;
    text-transform: uppercase;
    &:hover {
      color: #F07A34;
    }
  }
  &__email {
    font-size: 17px;
    line-height: 120%;
    font-weight: 300;
    letter-spacing: 1.02px;
    margin-top: 16px;
    &:hover {
      color: #F07A34;
    }
  }
  &__location {
    font-size: 17px;
    line-height: 120%;
    font-weight: 300;
    letter-spacing: 1.02px;
    margin-top: 60px;
  }
  &__socials {
    display: flex;
    justify-content: flex-end;
    gap: 40px;
    margin-top: 80px;
  }
  &__social {
    max-width: 20px;
    img {
      max-width: 100%;
    }
  }
}
.footer-bottom {
  padding-bottom: 140px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
  }
  &__logo {
    width: 188px;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 17px;
    line-height: 120%;
    font-weight: 300;
    color: #696971;
    letter-spacing: 1.02px;
  }
}

@media screen and (max-width: 1300px) {
  .footer-top {
    padding: 60px 0;
    &__items {
      gap: 80px;
    }
    &__list {
      gap: 24px;
      li a {
        font-size: 13px;
        letter-spacing: 1.08px;
      }
    }
    &__phone {
      font-size: 30px;
      letter-spacing: 1.8px;
    }
    &__email {
      font-size: 14px;
      letter-spacing: 0.84px;
    }
    &__location {
      font-size: 14px;
      letter-spacing: 0.84px;
      margin-top: 40px;
    }
    &__soacials {
      gap: 32px;
      margin-top: 60px;
    }
  }
  .footer-bottom {
    &__logo {
      width: 158px;
    }
    &__text {
      font-size: 14px;
      letter-spacing: 0.84px;
    }
  }
}

@media screen and (max-width: 760px) {
  .footer-bottom {
    padding-bottom: 80px;
  }
}

@media screen and (max-width: 759px) {
  .footer-top {
    &__inner {
      flex-direction: column;
      gap: 80px;
    }
    &__right {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }
  .footer-bottom {
    &__inner {
      justify-content: center;
    }
    &__logo {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
 .footer-bottom {
    padding-bottom: 60px;
  }
}

@media screen and (max-width: 429px) {
  .footer-top {
    padding: 60px 0;
    &__items {
      gap: 40px;
    }
  }
}